.card-services{
  background-color: var(--cover-color);
  border-radius: 12px;
  padding: 24px;
  text-align: center; 
  box-shadow: 0 4px 10px rgba(71, 71, 71, 0.4);
  transition:var(--main-transition);
  cursor: pointer;
}

.card-services:hover{
  transform: scale(1.1);
}

.card-services h1{
  font-size: 28px;
  font-weight: bold;
  color: var(--main-color);
}


.card-services img{
 max-width: 124px;

}

@media (max-width:767px) {
  .card-services h1{
    font-size: 20px;
  }
  .card-services img{
    max-width: 60px;
   }
   .card-services{

    padding: 10px;
   }
}