 .prog-holder{
  margin: 20px 80px;
  
}

 .prog-holder h4{
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: normal;
  color: var(--cover-color);
}

 .prog-holder .prog{
  position: relative;
  background-color: #cecece;
  height: 30px;
  
}

.prog span::before {
  content: attr(data-progress); 
  position: absolute;
  right: 10px; 
  top: -36px;
  display: flex;
  align-items: center;
  color: var(--main-color);
  background-color: var(--cover-color);
  font-size: 14px;
  border-radius: 12px;
  padding: 5px 10px;
}

 .prog-holder .prog span{
  display: block;
  background-color: var(--main-color);
  height: 100%;
}