.card-portfolio{
  display: flex;
  gap :20px;
  align-items: center;
  background: var(--cover-color);
  border-radius: 12px;
  ;
}

.card-portfolio .image-card{
  max-width: 50%;
  border-radius: 12px;
}

.card-portfolio h1{
  color: var(--main-color);
}

.card-portfolio p{
  color: white;
  margin-bottom:80px ;
}

@media (max-width:767px) {
  .card-portfolio{
    flex-direction: column;
    gap: 10px;
  }
}