.pagination{
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-link{
  background-color: var(--cover-color);
  color: var(--main-color);
  margin: 5px;
}

.page-link:hover,
.page-item.active .page-link{
  background-color: var(--main-color);
  color: white;
  box-shadow: none;
  border: none;
}

