.footer{
  background-color: var(--cover-color);
}

.footer-container{
  grid-template-columns: repeat(auto-fill , minmax(250px ,1fr));
  text-align: center;
}

.footer-container img{
  width: 200px;
  margin: auto;
}
.info h2{
  color:var(--main-color);
  margin-bottom: 10px;
}

.info a{
  text-decoration: none;
  color: white;
}
.footer-socia h3{
  color: var(--main-color);
}

.footer-socia a{
  margin: 10px;
  text-decoration: none;
  color: white;
}

.footer p{
  color: white;
}

.footer p span{
  color: var(--main-color);
} 