.AboutContainer{
  justify-content: space-between;
  align-items: center;
}

.AboutContainer img{
  width: 350px;
  flex-basis: 50%;
  border-radius: 12px;
}
.AboutContainer .About-info{
  flex-basis: 50%;
  
}
.AboutContainer .About-info h2{
  font-size: 36px;
  font-weight: bold;
  color: var(--main-color);
}

.AboutContainer .About-info p{
  font-size: 28px;
  line-height: 1.8;
}

@media (max-width:767px) {
  .AboutContainer{
    flex-direction: column;
  }
  .AboutContainer .About-info h2{
    font-size: 24px;
  }
  .AboutContainer img{
    width: 250px;
    border-radius: 12px;
  }
  .AboutContainer .About-info p{
    font-size: 14px;

  }
}



