.Cv-section{
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 100px;
}
.cv-container{
  text-align: center;
  background-color: var(--cover-color) ;
  padding: 120px;
}

.cv-container p{
  color: white;
  font-weight: bold;
}

.cv-container .btn{
  display: block;
  margin: auto;
  padding: 10px 180px;
  font-weight: bold;
}

@media (max-width:767px){
  .cv-container .btn{
    padding: 10px 60px;

  }

  .cv-container{
    padding: 60px;
  }
}