.navbar{
  background-color: var(--cover-color);
  height: 80px;
}
.navbar-brand img{
  height: 60px;
}

.navbar-nav li .nav-link{
  color: var(--main-color);
  transition: var(--main-transition);
  font-weight: bold;
  font-size: 16px;
}

.navbar-nav li .nav-link:hover{
  color: white;
}
.cv{
  font-weight: bold;
  transition: var(--main-transition);
  font-size: 14px;
  box-shadow: none;
}

.navbar-toggler{
  color:var(--main-color);
}

.navbar-toggler-icon{
  color: var(--main-color);

}
.navbar-toggler:focus{
  box-shadow: none;
}

.collapse{
  background-color: rgba(0, 0, 0, 0.616);
  z-index: 999;
}
