.ContactContainer{
  background-color: var(--cover-color);
  border-radius: 12px;
  padding: 24px;
  text-align: center; 
  box-shadow: 0 4px 10px rgba(71, 71, 71, 0.4);
  transition:var(--main-transition);
  cursor: pointer;
}

.ContactContainer:hover{
  transform: scale(1.1);
}

.ContactContainer h3{
  font-size: 32px;
  font-weight: bold;
  color: var(--main-color);
}

.ContactContainer a{
  text-decoration: none;
  color: white;
  margin: 10px;
}