.hero{
  background: linear-gradient(to right, #D7B500, #D7B500);
  height: 100vh;
}

.hero img{
  height: 570px;
  position: relative;
  bottom: -38px;
}

.hero .hero-button{
  border: 2px solid black;
  font-weight: bold;
  transition: var(  --main-transition);
  box-shadow: none;
  
}

.hero span,
.hero h1,
.hero p,
.hero .btn{
  font-weight: bold;
  color: #000;
}

.btn:hover{
  color:white;
  background-color: black;
 }

.hero span{
  font-size: 20px;
}
.hero h1{
  font-size: 48px;
  margin-bottom: 16px;
}

.hero p{
  font-size: 20px;
}

@media(max-width:767px){
  .hero img{
    display: none;
  }
  .hero span{
    font-size: 14px;
  }
  .hero h1{
   font-size: 28px;
  }
  .hero p{
    font-size: 18px;
  }
}


